

.user-data-main-container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 5;
    background-color: rgb(255, 255, 255);
    opacity: 0.95;
    border-radius: 10px;
    padding: 20px;
    width: 600px;
    height: 600px;
    border: solid 1px rgb(38, 2, 245);
    font-family: roboto, sans-serif;
    animation-name: fadeIn;
    animation-iteration-count: 1;
    animation-timing-function: ease-in-out;
    animation-duration: 0.4s;

    .header {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;

        h2 {
            margin-left: 10px;
        }
    }

    p {
        margin: 10px;
        color: rgb(155, 155, 155);
    }
    
    span {
        color: rgb(48, 47, 47);
    }

    img {
        width: 220px;
        height: 180px;
        border-radius: 10px;
        object-fit: cover;
    }

    @keyframes fadeIn {
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }

}